@import url(https://fonts.googleapis.com/css?family=Montserrat:100,100i,200,200i,300,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
}

body {
  background-color: aquamarine;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.post-feed {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 10px;
  gap: 10px;
  padding-top: 15px;
  padding-bottom: 15px;
  width: 1000px;
}

.container {
    width: 600px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding-top: 15px;
    border: 1px solid;
    border-radius: 10px;
    background-color: #ffffff;
}

.user-avatar {
  min-width: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 50%;
    width: 50px;
    height: 50px;
}


.post-body {
  padding-right: 12.5px;
  font-size: 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 20px;
}
.user-info{
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: baseline;
          align-items: baseline;
  grid-gap: 15px;
  gap: 15px;
  width: 100%;
  font-family: 'Montserrat', sans-serif;
}

.user-name {
  font-weight: bold;
}


.post-text {
  font-family: 'Montserrat', sans-serif;
  margin-top: 15px;
  width: 100%;
}

.post-image {
  border-radius: 15px;
  width: 100%; 
  display: block;
  margin-top: 15px;
}


